<template>
    <div class="container">
        <div class="controls">
            <div class="search-box">
                <i class="fas fa-search"></i>
                <input type="text" v-model="searchQuery" placeholder="搜索项目名称、描述或技术...">
            </div>

            <div class="filter-buttons">
                <button v-for="(filter, index) in filters" :key="index" @click="setActiveFilter(filter)"
                    :class="['filter-btn', { active: activeFilter === filter }]">
                    {{ filter }}
                </button>
            </div>
        </div>

        <div class="counter">
            显示 <span class="pulse">{{ filteredProjects.length }}</span> 个项目（共 {{ projects.length }} 个）
        </div>

        <div class="projects-grid">
            <div v-for="project in filteredProjects" :key="project.id" class="project-card">
                <div class="project-img" :style="{ backgroundImage: `url(${project.image})` }">
                    <div class="overlay">
                        <div>
                            <h3 class="project-title">{{ project.title }}</h3>
                        </div>
                    </div>
                </div>

                <div class="project-content">
                    <div class="tags">
                        <span v-for="(tag, index) in project.tags" :key="index" class="tag">
                            {{ tag }}
                        </span>
                    </div>

                    <p class="project-description">
                        {{ project.description }}
                    </p>

                    <div class="project-meta">
                        <div class="project-lang">
                            <span class="lang-color" :style="{ backgroundColor: project.langColor }"></span>
                            {{ project.language }}
                        </div>
                        <div class="project-stats">
                            <div class="stat">
                                {{ project.description1 }}
                            </div>
                            <div class="stat">
                                {{ project.description2 }}
                            </div>
                        </div>
                    </div>
                </div>

                <a :href="project.url" target="_blank" class="project-link">
                    查看项目
                </a>
            </div>

            <div v-if="filteredProjects.length === 0" class="no-results">
                <i class="fas fa-search fa-2x" style="margin-bottom: 20px;"></i>
                <h3>未找到匹配的项目</h3>
                <p>请尝试其他搜索词或重置筛选条件</p>
            </div>
        </div>
    </div>
</template>

<script setup>
import { ref, computed } from "vue"
import { projects } from "@/assets/ProjectData";

// 过滤选项
const filters = ref(["全部", "C++", "Python", "vue", "HTML", "大作业"]);
const activeFilter = ref("全部");
const searchQuery = ref("");

// 设置活动过滤器
const setActiveFilter = (filter) => {
    activeFilter.value = filter;
};

// 计算过滤后的项目
const filteredProjects = computed(() => {
    return projects.filter(project => {
        // 搜索筛选
        const matchesSearch = searchQuery.value === "" ||
            project.title.toLowerCase().includes(searchQuery.value.toLowerCase()) ||
            project.description.toLowerCase().includes(searchQuery.value.toLowerCase()) ||
            project.tags.some(tag => tag.toLowerCase().includes(searchQuery.value.toLowerCase()));

        // 类别筛选
        const matchesFilter = activeFilter.value === "全部" ||
            project.language.toLowerCase().includes(activeFilter.value.toLowerCase()) ||
            project.tags.some(tag => tag.toLowerCase().includes(activeFilter.value.toLowerCase()));

        return matchesSearch && matchesFilter;
    });
});
</script>


<style scoped>
